import { Suspense } from "react";
import RectangleTabWrapper from "../../Ui/Tab/Rectangle/RectangleTabWrapper";
import RectangleTabCard from "../../Ui/Tab/Rectangle/RectangleTabCard";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import SquareTabWrapper from "../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../Ui/Tab/Square/SquareTabCard";
import { assestsStoreUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import Loading from "../../Loader/Loading";
import OtherCommitteeMeeting from "./OtherCommitteeMeeting/OtherCommitteeMeeting";
import OtherCommitteeAbout from "./OtherCommitteeAbout/OtherCommitteeAbout";

const CommitteeOtherManagerProfile = ({ qcid }) => {
  const params = useParams();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  let measure = document.getElementById("7892bdsb")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 870 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 870 ? 1 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }
  return (
    <>
      <RectangleTabWrapper>
        {tabList.map((tab) => (
          <RectangleTabCard
            key={tab?.id}
            label={tab.label}
            isLink
            isActive={
              tab.id === "menu" && !searchParams.get("type")
                ? true
                : searchParams.get("type") === tab.id
                ? true
                : false
            }
            parentState={getQuery.state}
            url={`/q/${params.username}/member/staff/committee/other${tab.urlKey}`}
            isAuthorized
          />
        ))}
      </RectangleTabWrapper>

      {!searchParams.get("type") ? (
        <SquareTabWrapper dynamicId="7892bdsb">
          <SquareTabCard
            label="compositions"
            iconUrl={`${assestsStoreUrl}/unit.svg`}
            url={`/q/${params.username}/member/staff/committee/other?type=member`}
            state={getQuery.state}
            isAuthorized
            // isVisible={tabManage ?? {}}
            isVisible={{
              store_unit: true,
            }}
            authorizedKey="store_unit"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
        </SquareTabWrapper>
      ) : searchParams.get("type") === "about" ? (
        <Suspense fallback={<Loading />}>
          <OtherCommitteeAbout qcid={qcid} />
        </Suspense>
      ) : searchParams.get("type") === "meeting" ? (
        <Suspense fallback={<Loading />}>
          <OtherCommitteeMeeting qcid={qcid} />
        </Suspense>
      ) : null}
    </>
  );
};

export default CommitteeOtherManagerProfile;

const tabList = [
  {
    label: "menu",
    id: "menu",
    urlKey: "",
  },
  {
    label: "meeting",
    id: "meeting",
    urlKey: "?type=meeting",
  },
  {
    label: "about",
    id: "about",
    urlKey: "?type=about",
  },
];
