import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useState } from "react";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddOtherCommitteeAbout from "./AddOtherCommitteeAbout";
import OtherCommitteeAboutCard from "./OtherCommitteeAboutCard";
import Loading from "../../../Loader/Loading";
import { useIqacDashboardMaster } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
const OtherCommitteeAbout = ({ qcid }) => {
  const [add, setAdd] = useState(false);

  const {
    iqacDashboardMaster,
    iqacDashboardMasterLoading,
    iqacDashboardMasterRefetch,
  } = useIqacDashboardMaster({
    data: {
      qcid: qcid,
    },
    skip: !qcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  //
  // console.info("sdjkhgfvsdj", iqacDashboardMaster?.about);
  return (
    <>
      <div
        className={style.fm_header_container}
        style={{
          marginTop: "1rem",
        }}
      >
        <h6></h6>
        <CreateButton
          label="add"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onToggle}
        />
      </div>
      <BorderBottom />

      {iqacDashboardMaster?.about?.map((sub) => (
        <OtherCommitteeAboutCard sub={sub} key={sub?._id} />
      ))}
      {iqacDashboardMasterLoading && <Loading />}

      {add && (
        <AddOtherCommitteeAbout
          onClose={onToggle}
          onRefetch={iqacDashboardMasterRefetch}
          qcid={qcid}
        />
      )}
    </>
  );
};

export default OtherCommitteeAbout;
